import Swiper, { EffectCoverflow, Pagination } from 'swiper'
import 'swiper/css'
import 'swiper/css/effect-coverflow'
import 'swiper/css/effect-fade'

const swipers = {
  downloadImagesSwiper: () =>
    new Swiper('.download-images__swiper', {
      slidesPerView: 1,
      spaceBetween: 20,
      centeredSlides: true,
      loop: true,
      autoplay: {
        delay: 4000,
      },
      direction: 'vertical',
      modules: [EffectCoverflow],
      effect: 'coverflow',
      coverflowEffect: {
        scale: 0.8,
        stretch: -20,
        depth: 50,
        rotate: 0,
        slideShadows: false,
        modifier: 1,
      },
      breakpoints: {
        992: {
          coverflowEffect: {
            stretch: 260,
          },
          direction: 'horizontal',
          slidesPerView: 2,
        },
      },
    }),
  downloadItemsSwiper: () =>
    new Swiper('.download-items__swiper', {
      slidesPerView: 1,
      spaceBetween: 20,
      modules: [Pagination],
      pagination: {
        el: '.download-items__swiper-pagination.swiper-pagination',
        clickable: true,
      },
      breakpoints: {
        768: {
          slidesPerView: 2,
        },
        992: {
          slidesPerView: 3,
        },
      },
    }),
}

export default swipers
